<template>
  <div class="login">
    <van-overlay :show="loading" @click="loading = false" />
    <van-loading color="#1989fa" v-if="loading" class="loading" />
    <van-form @submit="onSubmit" class="login-form">
      <van-field
        v-model="username"
        name="用户名"
        label="用户名"
        left-icon="orders-o"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        left-icon="closed-eye"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />

      <van-button
        v-if="islogin"
        round
        block
        type="info"
        native-type="submit"
        style="height: 50px;font-size: 18px;"
        >登录</van-button
      >

      <van-button
        v-if="!islogin"
        round
        block
        type="info"
        native-type="submit"
        style="height: 50px;font-size: 18px;"
        >注册</van-button
      >

      <van-button
        v-if="!islogin"
        round
        block
        type="info"
        style="height: 50px;font-size: 18px; margin-top: 15px"
        @click="gologin"
        >去登陆</van-button
      >
    </van-form>

    <div class="register">
      <div style="color: darkgrey;">&nbsp;还没有注册？</div>
      <div style="font-weight: 600;" @click="register">现在注册</div>
    </div>
    <div>
      {{error}}
    </div>
  </div>
</template>

<script>
import { Notify } from "vant";
export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      loading: false,
      islogin: true,
      userlist: [],
      error: 'err'
    };
  },
  mounted() {
    window.onresize = () => {
      window.screenWidth = document.body.clientWidth;
      this.screenWidth = window.screenWidth;
    };
  },
  methods: {
    gologin() {
      this.islogin = true;
    },
    register() {
      this.islogin = false;
    },
    loginIn() {
      const baselist = [
        {
          username: "jkk",
          password: "123",
        },
      ];
      let storage = window.localStorage.getItem("userlist");
      this.userlist = storage ? JSON.parse(storage).concat(baselist) : baselist;
      const loginsuccess = this.userlist.some((user) => {
        return (
          user.username === this.username && user.password === this.password
        );
      });
      if (!loginsuccess) {
        Notify({ type: "danger", message: "用户名或密码错误" });
        return;
      }
      this.$emit("loginIn", true);
    },
    onSubmit() {
      this.loading = true;
      if (this.islogin) {
        try {
          this.loading = false;
          window.localStorag.setItem("user", this.username);
          setTimeout(() => {
            this.loading = false;
            this.loginIn();
          }, 1000);
        } catch (error) {
          this.error = error
        }
      } else {
        if (!this.username || !this.password) {
          Notify({ type: "warning", message: "请输入用户名和密码" });
          return;
        }

        let storage = window.localStorage.getItem("userlist");
        let userlist = storage ? JSON.parse(storage) : [];

        if (userlist.some((user) => user.username === this.username)) {
          Notify({ type: "warning", message: "此用户名已被注册" });
          return;
        }

        userlist.push({
          username: this.username,
          password: this.password,
        });
        window.localStorage.setItem("userlist", JSON.stringify(userlist));
        Notify({ type: "success", message: "注册成功" });
        this.loading = false;
      }
    },
  },
};
</script>

<style scoped>
.login {
  height: 100%;
  background: #ddd;
  position: relative;
}

.login-form {
  position: absolute;
  left: 10%;
  margin-top: 30vh;
  width: 80%;
}

.login .van-cell {
  margin: 20px 0;
  border-radius: 50px;
  padding: 15px;
}

.login .loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.register {
  position: fixed;
  bottom: 20vw;
  left: 50%;
  transform: translateX(-50%);
  line-height: 26px;
}
</style>
